import {iosVhFix} from './utils/ios-vh-fix';
import {initModals} from './modules/modals/init-modals';
import {initAccordions} from './modules/init-accordion';
import {initTabs} from './modules/init-tabs';
// ---------------------------------

window.addEventListener('DOMContentLoaded', () => {

  // Utils
  // ---------------------------------

  iosVhFix();

  // Modules
  // ---------------------------------

  // все скрипты должны быть в обработчике 'DOMContentLoaded', но не все в 'load'
  // в load следует добавить скрипты, не участвующие в работе первого экрана
  window.addEventListener('load', () => {
    initModals();
    initAccordions();
    initTabs();

    $('input[type="tel"]').inputmask('+7 999 999 99 99');

    $('[data-burger]').on('click', function() {
      let $this = $(this),
          $list = $('[data-burger-list]');
          // $overlay = $('.header-overlay');

      // $('html').toggleClass('of-hidden');
      $this.toggleClass('is-active');
      $list.toggleClass('is-active');
      // $overlay.fadeIn(300);
    });

    let reviewSlider = document.querySelectorAll('.reviews__slider');
    if(reviewSlider) {
      reviewSlider.forEach(function(slider) {
        let swiper = new Swiper(slider, {
          spaceBetween: 10,
          loop: true,

          navigation: {
            prevEl: '.reviews__arrow.--prev',
            nextEl: '.reviews__arrow.--next'
          },

          breakpoints: {
            1031: {
              slidesPerView: 1
            },
            1031: {
              slidesPerView: 2
            },
          }
        });
      });
    }

    if(window.innerWidth > 1030) {
      let partners = document.querySelectorAll('.partners__slider');
      if(partners) {
        partners.forEach(function(item) {
          let swiper = new Swiper(item, {
            slidesPerView: 7,
            spaceBetween: 30,
            speed: 3000,
            loop: true,
            autoplay: {
              delay: 0,
            },
          });
        });
      }
    }
    
    
  });
});

